<template>
   <div class="box">
        <div class="box-header with-border">
            <h3 class="box-title">Danh sách sản phẩm đã duyệt</h3>
            <div class="box-tools" style="display: flex;">
                <div style="width:150px;">
                    <Select2 v-model="pageSize.myValue" :options="pageSize.myOptions" @change="changePageSize($event)" />
                </div>
                <div style="width:150px;">
                    <Select2 v-model="orderBy.myValue" :options="orderBy.myOptions" @change="changeOrderBy($event)" />
                </div>
            </div>
            <div class="row search">
                <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
                    <Select2 v-model="province.myValue" :options="province.myOptions" @change="changeProvince($event)" />
                </div>
                <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
                    <Select2 v-model="district.myValue" :options="district.myOptions" @change="changeDistrict($event)" />
                </div>
                <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
                    <Select2 v-model="category.myValue" :options="category.myOptions" @change="changeCategory($event)" />
                </div>
                <div class="col-lg-3 col-md-3 col-sm-3 col-xs-3">
                    <input v-on:keyup="search" type="text" class="form-control" placeholder="Tìm kiếm...">                    
                </div>
            </div>
        </div>
        <div class="box-body table-responsive">
            <table class="table table-hover">
                <tr>
                    <th>ID <input v-on:click="choose" type="checkbox" id="choose"></th>
                    <th style="width:136px;">Ngày đăng</th>
                    <th style="width:104px;">Danh mục</th>
                    <th style="width:81px;">SDT</th>
                    <th>Tên</th>
                    <th>Giá</th>
                    <th>Địa chỉ</th>
                    <th style="width:100px;">Hình</th>
                    <th>Tài khoản</th>
                    <th style="width:74px;">Phê duyệt</th>
                    <th style="width:74px;">feedback</th>
                    <th style="width:132px;">Chức năng</th>
                </tr>
                <tr v-for="(item, index) in product" :key="index">
                    <td><b>{{ item.id }}</b> <input v-model="id_product" type="checkbox" name="id_product" :value="`${item.id}/${index}/${item.image}`" class="choose"></td>
                    <td>
                        <div style="color:red">{{ getTime(item.date).fromNow() }}</div>
                        <div>{{ getTime(item.date).format('llll') }}</div>
                    </td>
                    <td>{{ item.category }}</td>
                    <td>{{ item.phone }}</td>
                    <td>{{ item.name }}</td>
                    <td>{{ number_format(item.price,0) }} đ</td>
                    <td>{{ item.province + " - " + item.district + " - " + item.address }}</td>
                    <td><img class="product-img" v-bind:src="api + '/image/product/thumbnail/' + item.image"></td>
                    <td>
                        {{ item.username }}
                    </td>
                    <td>
                        <span v-if="item.status==1">Đã duyệt</span>
                        <span v-if="item.status==3">Đã báo xấu</span>
                    </td>
                    <ShowFeedback :item="item" />
                    <td>
                        <router-link :to="{name: 'route.product', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id, product: item.url_product}}" target="_blank" class="btn btn-info">Xem thử</router-link>
                        <button v-if="item.status==1" v-on:click="block(item.id, index)" class="btn btn-warning">Báo xấu</button>
                        <button v-on:click="del(item.id, index, item.image)" class="btn btn-danger">Xóa</button>
                    </td>
                </tr>
            </table>
            <div class="menu">
                <button v-on:click="blockSuper" class="btn btn-warning">Báo xấu</button>
                <button v-on:click="delSuper" class="btn btn-danger">Xóa</button>
            </div>
            <paginate v-model="paginate.page"
                :page-count="paginate.pageCount"
                :page-range="3"
                :margin-pages="2"                                           
                :click-handler="runPage"
                :prev-text="`<i class='fa fa-angle-left' aria-hidden='true'></i>`"
                :next-text="`<i class='fa fa-angle-right' aria-hidden='true'></i>`"
                :container-class="'pagination'"
                :page-class="'page-item'">
            </paginate>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import paginate from "vuejs-paginate";
import Select2 from 'v-select2-component';
import ShowFeedback from './ShowFeedback';

export default {
    metaInfo: {
        title: "Sản phẩm đã duyệt"
    },
    components: {paginate, Select2, ShowFeedback},
    data(){
        return {
            api: this.$store.state.domainApi,
            product:[],
            paginate:{
                page:1,
                pageCount:10
            },
            pageSize:{
                myValue:10,
                myOptions:[
                    {id:10, text:"page size: 10"},
                    {id:20, text:"page size: 20"},
                    {id:50, text:"page size: 50"},
                    {id:100, text:"page size: 100"},
                    {id:200, text:"page size: 200"}
                ]
            },
            orderBy:{
                myValue: 'DESC',
                myOptions: [
                    {id: 'ASC', text: 'Tăng dần'},
                    {id: 'DESC', text: 'Giảm dần'}
                ]
            },
            dataProvince: [],
            province:{
                myValue:'',
                myOptions:[]
            },
            district:{
                myValue:'',
                myOptions:[]
            },
            category:{
                myValue:'',
                myOptions:[],
                value:''
            },
            id_product:[]
        }
    },
    methods: {
        getTime(date){
            var time = moment(date);
            return time;
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        block(id_product, index){
            axios.put(this.api + "/admin/product",{
                id_product: [id_product],
                status: 3
            },{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response=>{
                if(response.data.success){
                    this.product[index].status=3;
                    this.$store.state.admin.product.feedback--;
                    this.$store.state.admin.product.block++;
                    this.flash("Báo xấu thành công !", 'success', {
                        timeout: 3000
                    });
                }else{
                    this.flash(response.data.message, 'error', {
                        timeout: 3000
                    });
                }
            })
            .catch(error=>{
                console.log(error);
                this.flash('Có lỗi kết nối đến api !', 'error', {
                    timeout: 3000
                });
            })
        },
        blockSuper(){
            if(this.id_product.length > 0)
            this.$swal.fire({
                title: 'Bạn có muốn báo xấu các sản phẩm này không?',
                text: "Báo xấu toàn bộ sản phẩm",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không !',
                confirmButtonText: 'Có, Báo xấu hết !'
            }).then((result) => {
                if(result.value){
                    var obj = this.id_product.reduce((res, val)=>{
                        var arr = val.split('/');
                        res.id_product.push(arr[0]);
                        res.index.push(arr[1]);
                        return res;
                    },{id_product:[], index:[]});

                    axios.put(this.api + "/admin/product",{
                        id_product: obj.id_product,
                        status: 3
                    },{
                        headers:{
                            Authorization: `Bearer ${localStorage.token}`
                        }
                    })
                    .then(response=>{
                        if(response.data.success){
                            obj.index.forEach(e=>{
                                this.product[e].status=3;
                                this.$store.state.admin.product.feedback--;
                                this.$store.state.admin.product.block++;
                            });

                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                    })
                }
            });
        },
        del(id_product, index, image){
            this.$swal.fire({
                title: 'Bạn có muốn xóa sản phẩm này không?',
                text: "Xóa sản phẩm",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không, Chưa xóa !',
                confirmButtonText: 'Có, Xóa !'
            }).then((result) => {
                if(result.value)
                axios.delete(this.api + "/admin/product",{
                    headers:{
                        Authorization: `Bearer ${localStorage.token}`
                    },
                    params:{
                        id_product: [id_product],
                        image: [image]
                    }
                })
                .then(response=>{
                    if(response.data.success){
                        this.product.splice(index,1);
                        this.$store.state.admin.product.feedback--;
                        this.flash(response.data.message, 'success', {
                            timeout: 3000
                        });
                    }else{
                        this.flash(response.data.message, 'error', {
                            timeout: 3000
                        });
                    }
                });
            });
        },
        delSuper(){
            if(this.id_product.length > 0)
            this.$swal.fire({
                title: 'Bạn có muốn xóa các sản phẩm này không?',
                text: "Xóa toàn bộ danh mục con và sản phẩm",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không, Chưa xóa !',
                confirmButtonText: 'Có, Xóa hết !'
            }).then((result) => {
                if(result.value){
                    var obj = this.id_product.reduce((res, val)=>{
                        var arr = val.split('/');
                        res.id_product.push(arr[0]);
                        res.index.push(arr[1]);
                        res.image.push(arr[2]);
                        return res;
                    },{id_product:[], index:[], image:[]});

                    axios.delete(this.api + "/admin/product",{
                        headers:{
                            Authorization: `Bearer ${localStorage.token}`
                        },
                        params:{
                            id_product: obj.id_product,
                            image: obj.image
                        }
                    })
                    .then(response=>{
                        if(response.data.success){
                            obj.index.forEach(e=>{
                                this.product.splice(e,1);
                                this.$store.state.admin.product.feedback--;
                            });

                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                    })
                }
            });
        },
        choose(e){
            var id_product = [];
            document.getElementsByClassName("choose").forEach(element => {
                element.checked = e.target.checked;
                id_product.push(element.value);
            });
            if(e.target.checked) this.id_product = id_product;
            else this.id_product = [];
        },
        async search(event){
            var response = await axios.get(this.api + "/admin/product",{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                },
                params:{
                    province: this.province.myValue,
                    district: this.district.myValue,
                    category: this.category.value,
                    status: 1,
                    search: event ? event.target.value : '',
                    feedback: true,
                    page: this.paginate.page,
                    pageSize: this.pageSize.myValue,
                    orderBy: this.orderBy.myValue
                }
            });
            this.product = response.data.product;
            this.paginate.pageCount = Math.ceil(response.data.count/this.pageSize.myValue);
        },
        runPage(page){
            this.paginate.page = page;
            this.search('');
        },
        changePageSize(){
            this.search('');
        },
        changeOrderBy(){
            this.search('');
        },
        changeProvince(value){
            var obj = { id: "tat-ca-quan-huyen", text: "Tất cả quận huyện"};
            this.district.myValue =  value == 'toan-quoc' ? '' : 'tat-ca-quan-huyen';

            this.district.myOptions = 
            value == 'toan-quoc' ? this.customText(
                this.dataProvince.filter(e=>e.parent_id == value)
            ) : [obj].concat(
                this.customText(
                    this.dataProvince.filter(e=>e.parent_id == value)
                )
            );
            this.search('');
        },
        changeDistrict(){
            this.search('');
        },
        changeCategory(value){
            var arrCat = this.find(
                this.$store.state.category, value
            ).reduce((result, id_category)=>result + `,${id_category}`, value);
            this.category.value = arrCat;
            
            this.search('');
        },
        customText(data){
            return data.reduce((result, item)=>{
                let custom = {
                    id: item.id,
                    text: item.name
                }
                return result.concat([custom]);
            }, []);
        },
        formatJson(data, parent_id=0, str=""){
            var parent = [];
            for(var item of data){
                if(item.parent_id == parent_id){
                    var obj = {
                        id: item.id,
                        text: str + " " + item.name
                    }
                    parent.push(obj);
                    var result = this.formatJson(data, item.id, str + "-----|");
                    parent = parent.concat(result);
                }
            }
            return parent;
        },
        find(data, parent_id){
            var parent = [];
            for(var item of data){
                if(item.parent_id==parent_id){
                    parent.push(item.id);
                    var result = this.find(data, item.id);
                    parent = parent.concat(result);
                }
            }
            return parent;
        }
    },
    async created(){
        moment.locale("vi");

        var response = await axios.get(this.api + "/admin/product",{
            headers:{
                Authorization: `Bearer ${localStorage.token}`
            },
            params:{
                status:1,
                feedback: true,
                pageSize: this.pageSize.myValue,
                orderBy: this.orderBy.myValue
            }
        });
        this.product = response.data.product;
        this.paginate.pageCount = Math.ceil(response.data.count/this.pageSize.myValue);

        this.dataProvince = this.$store.state.province.map(e => e);
        var obj = { id: "toan-quoc", text: "Toàn quốc" };
        this.province.myValue = 'toan-quoc';
        this.province.myOptions =  [obj].concat(
            this.customText(
                this.dataProvince.filter(e=>e.parent_id == 0)
            )
        );

        obj = [{id: "tat-ca-danh-muc", text: "Tất cả danh mục"}];
        this.category.myValue = 'tat-ca-danh-muc';
        this.category.myOptions = obj.concat(
            this.formatJson(
                this.$store.state.category.map(e => e)
            )
        );
    }
}
</script>

<style>
.row.search{
    margin: 6px 0px 0px;
}
.row.search .col-lg-3{
    padding: 0px;
}
table.table tr td, table.table tr th{
    padding: 10px 5px;
    border: 1px solid #ecf0f5;
}
img.product-img{
    height: 65px;
    width: 100px;
    object-fit: cover;
}
#choose, .choose{
    position: relative;
    height: 20px;
    width: 20px;
    top: 5px;
}
.menu{
    display: inline-block;
    margin-top: 20px;
}
.pagination{
    display: inline-block;
    float: right;
}
</style>