<template>
    <td>
        <a v-on:click="show" class="btn btn-default">{{ item.count_feedback ? item.count_feedback : 0 }} Báo xấu</a>
        <div :class="{ modal: true, fade: true, in: true ,show: menuLoc.status }">
            <div class="modal-dialog">
                <transition name="slide-fade">
                    <div v-if="menuLoc.content" class="modal-content">
                        <form @submit.prevent="submitSort">
                            <div class="modal-header">
                                <button type="button" class="close" v-on:click="hide">
                                <span aria-hidden="true">&times;</span></button>
                                <h4 class="modal-title">Danh sách báo xấu</h4>
                            </div>
                            <div class="modal-body">
                                <div class="form-group">
                                    <label>ID sản phẩm: </label> {{item.id}}
                                </div>
                                <div class="form-group">
                                    <label>Tên sản phẩm: </label> {{item.name}}
                                </div>
                                <table style="width:100%">
                                    <tr>
                                        <th style="width:102px;">Ngày gửi</th>
                                        <th style="width:20%;">Tài khoản</th>
                                        <th>Nội dung</th>
                                    </tr>
                                    <tr v-for="obj in feedback" :key="obj.index">
                                        <td>
                                            <p style="color:red;">{{ getTime(obj.created_at).fromNow() }}</p>
                                            {{ getTime(obj.created_at).format("llll") }}
                                        </td>
                                        <td>{{ obj.username }}</td>
                                        <td><div v-html="obj.content"></div></td>
                                    </tr>
                                </table>
                            </div>
                        </form>
                    </div>
                </transition>
            </div>
        </div>
    </td>
</template>
<script>
import axios from 'axios';
import moment from 'moment';

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            menuLoc: {
                status: false,
                content: false
            },
            feedback:[]
        }
    },
    props:['item'],
    methods:{
        getTime(date){
            return moment(date);
        },
        show () {
            axios.get(this.api + "/admin/productfeedback/" + this.item.id,{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response=>{
                this.feedback = response.data;
            })
            this.menuLoc.status = true;
            this.menuLoc.content = true;
            document.querySelector("body").classList.add('modal-open');
        },
        hide () {
            this.menuLoc.content = false;
            document.querySelector("body").classList.remove('modal-open');
            setTimeout(() => {
                this.menuLoc.status = false;
            }, 400);
        }
    },
    created(){
        moment.locale("vi");
    }
}
</script>